export const formatTime = (value) => {
  let year, month, date, hour, minute;

  let a = value.split('T');
  let b = a[0].split('-');
  // year = +b[0];
  // month = +b[1];
  // date = +b[2];
  [year, month, date] = b;

  let c;
  if(a[1]) {
    c = a[1].split(':');
    // hour = +c[0];
    // minute = +c[1];
    [hour, minute,] = c;
  }

  let now = new Date();

  if(year < now.getFullYear()){
    return (now.getFullYear()-year) + '' ;
  } else if(month < now.getMonth()+1) {
    return (now.getMonth()+1 - month) + '';
  } else if(date < now.getDate()) {
    return (now.getDate() - date) + '';
  } else if(hour < now.getHours()) {
    return (now.getHours() - hour) + '';
  } else if (minute < now.getMinutes()) {
    return (now.getMinutes() - minute) + '';
  } else {
    return '';
  }
}

export const formatTab = (value, istop, isgood) => {
  if (istop) {
    return '';
  }
  if (isgood) {
    return '';
  }
  switch(value) {
    case 'share': return ''; break;
    case 'ask': return ''; break;
    default : return '';
  }
}



